import React, { useState } from 'react';
import './Dashboard.css';

const Dashboard = () => {
    const [isFormVisible, setIsFormVisible] = useState(true);
    const [userDataList, setUserDataList] = useState([]);

    const handleFormToggle = () => {
        setIsFormVisible(!isFormVisible);
    };

    const handleAddUser = (newUserData) => {
        setUserDataList((prevData) => [...prevData, newUserData]);
        setIsFormVisible(true);
    };

    return (
        <div className="dashboard">
            <h1 className="title">User Dashboard</h1>
            <button 
                onClick={handleFormToggle} 
                className="toggle"
            >
                {isFormVisible ? 'Hide' : 'Create'} User
            </button>
            {isFormVisible && <Form onAddUser={handleAddUser} />}
            <div className="table-container">
                <table className="table">
                    <thead>
                        <tr>
                            <th>Full Name</th>
                            <th>Age</th>
                            <th>Address</th>
                            <th>Email</th>
                        </tr>
                    </thead>
                    <tbody>
                        {userDataList.length > 0 ? (
                            userDataList.map((user, index) => (
                                <tr key={index}>
                                    <td>{user.fullName}</td>
                                    <td>{user.age}</td>
                                    <td>{user.address}</td>
                                    <td>{user.email}</td>
                                </tr>
                            ))
                        ) : (
                            <tr>
                                <td colSpan="4" className="no-data">No users added yet.</td>
                            </tr>
                        )}
                    </tbody>
                </table>
            </div>
        </div>
    );
};

const Form = ({ onAddUser }) => {
    const [fullName, setFullName] = useState('');
    const [age, setAge] = useState('');
    const [address, setAddress] = useState('');
    const [email, setEmail] = useState('');

    const handleSubmit = (e) => {
        e.preventDefault();
        const newUser = { fullName, age, address, email };
        onAddUser(newUser);
        setFullName('');
        setAge('');
        setAddress('');
        setEmail('');
    };

    return (
        <form onSubmit={handleSubmit}>
            <table className="form-table">
                <tbody>
                    <tr>
                        <th>Full Name</th>
                        <td><input type="text" value={fullName} onChange={(e) => setFullName(e.target.value)} required /></td>
                    </tr>
                    <tr>
                        <th>Age</th>
                        <td><input type="number" value={age} onChange={(e) => setAge(e.target.value)} required /></td>
                    </tr>
                    <tr>
                        <th>Address</th>
                        <td><input type="text" value={address} onChange={(e) => setAddress(e.target.value)} required /></td>
                    </tr>
                    <tr>
                        <th>Email</th>
                        <td><input type="email" value={email} onChange={(e) => setEmail(e.target.value)} required /></td>
                    </tr>
                </tbody>
            </table>
            <button type="submit">Add User</button>
        </form>
    );
};

export default Dashboard;
